import React from 'react';
import styles from './User.module.css'
import { NavLink } from "react-router-dom";

const User = (props) => {

    let avatarForAll = "data:image/jpeg;base64,"

    return (
        <div className={styles.user_item}>
            <NavLink to={"/profile/" + props.id}>
                <img src={(props.photos.small != null) ? props.photos.small : avatarForAll}//avatarForAll     ProfileInfo
                    className={styles.ava}
                    alt='' />
            </NavLink>
            <div>{props.id}</div>
            <div>{props.name}</div>
            <div>{props.status}</div>

            {
                props.followed ?
                    <button disabled={props.followingInProgress.includes(props.id)} onClick={() => {
                        props.onUnfollowButtonClick(props.id)
                    }}> 'Unfollow' </button> :

                    <button disabled={props.followingInProgress.some(id => id === props.id)} onClick={() => {
                        props.onFollowButtonClick(props.id)
                    }}>'Follow' </button>
            }
        </div>
    )
}

export default User;
