import React from 'react';
import styles from './Content.module.css'
import {Routes, Route} from 'react-router-dom'
//import Messages from "./Messages/Messages";
import ProfileContainer from "./Profile/ProfileContainer";
import DialogsContainer from "./Dialogs/DialogsContainer";
import UsersContainer from './Users/UsersContainer';
import LoginPage from "../Login/Login";
import {lazy} from 'react';
import {withSuspense} from "../../HOC/withSuspense";

const Messages = lazy(() => import('./Messages/Messages'));

const Content = (props) => {
    return (
        <div className={styles.content_wrapper}>
            <Routes>
                <Route basename={process.env.PUBLIC_URL} path="/dialogs/*" element={<DialogsContainer
                    store={props.store}/>}/>
                <Route basename={process.env.PUBLIC_URL} path="/messages/*" element={withSuspense(Messages)()}/>
                <Route basename={process.env.PUBLIC_URL}path="/profile/">
                    <Route basename={process.env.PUBLIC_URL} path=":userId" element={<ProfileContainer store={props.store}/>}/>
                    <Route basename={process.env.PUBLIC_URL} path="" element={<ProfileContainer store={props.store}/>}/>
                </Route>
                <Route basename={process.env.PUBLIC_URL} path="/users/*" element={<UsersContainer
                    store={props.store}/>}/>
                <Route basename={process.env.PUBLIC_URL} path="/login" element={<LoginPage/>}
                />

            </Routes>
        </div>
    )
        ;
}

export default Content;
