import React from 'react';
import styles from './PostItem.module.css'

const PostItem = (props) => {
    return (
        <div className={styles.postItem}>
            <div className={styles.postItem_faceAndCount}>
                <img className={styles.postItem_faceAndCount_face} src="data:image/jpeg;base64,
                    CM8rpgMWqkbret65M6SnYTEyg1KL1+viWfBY2NRcnxX4NePJ5cMmTHNeXUAC1UAAAAAAAAHxn0AYAA46j879leL+hBMAzZfs1Yvq1MxiAVLWNQ1xAOS7DfA+gHXWcTEADGqaJgHBtp7gwDqLKBIZZ/1I+KAJU7hG3UrIGfAbGIAAGYAOuP/9k="
                     alt="face"/>
                <div className={styles.postitem_faceAndCount_likeCount}>
                    Likes: {props.likesCount}
                </div>
            </div>

            <span className={styles.postText}>
                    {props.postText}
                </span>

            <button className={styles.postItem_button}>
                Like
            </button>
        </div>
    )
}

export default PostItem;
